'use client';
import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

export default function BlogStoryModal({
  isOpen,
  onClose,
}: {
  isOpen: boolean;
  onClose: () => void;
}) {
  const [form, setForm] = useState({
    name: '',
    email: '',
    title: '',
    content: ''
  });
  const [status, setStatus] = useState<'idle' | 'loading' | 'success' | 'error'>('idle');
  const [message, setMessage] = useState('');

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setStatus('loading');
    setMessage('');
    try {
      const res = await fetch(process.env.NEXT_PUBLIC_WEB3FORMS_ENDPOINT!, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          access_key: process.env.NEXT_PUBLIC_WEB3FORMS_ACCESS_KEY,
          name: form.name,
          email: form.email,
          subject: form.title,
          message: form.content,
        }),
      });
      const data = await res.json();
      if (data.success) {
        setStatus('success');
        setMessage('Thank you for sharing your story! Weâ€™ll review and get in touch soon.');
        setForm({ name: '', email: '', title: '', content: '' });
      } else {
        setStatus('error');
        setMessage('There was a problem submitting your story. Please try again.');
      }
    } catch {
      setStatus('error');
      setMessage('Network error. Please try again later.');
    }
  };

  // Prevent scrolling on modal open (optional)
  // useEffect(() => {
  //   if (isOpen) document.body.style.overflow = 'hidden';
  //   else document.body.style.overflow = '';
  // }, [isOpen]);

  return (
    <AnimatePresence>
      {isOpen && (
        <motion.div
          className="fixed inset-0 z-50 flex items-center justify-center bg-black/50"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
        >
          <motion.div
            initial={{ scale: 0.95, opacity: 0, y: 40 }}
            animate={{ scale: 1, opacity: 1, y: 0 }}
            exit={{ scale: 0.95, opacity: 0, y: 40 }}
            transition={{ type: "spring", duration: 0.35 }}
            className="bg-white rounded-xl shadow-lg p-8 max-w-lg w-full relative"
          >
            <button
              onClick={onClose}
              className="absolute top-4 right-4 text-gray-400 hover:text-red-500 text-2xl font-bold"
              aria-label="Close"
              type="button"
            >
              &times;
            </button>
            <h1 className="text-2xl font-bold text-green-800 mb-4 text-center">
              Submit Your Story
            </h1>
            <form onSubmit={handleSubmit} className="space-y-5">
              <div>
                <label htmlFor="name" className="block font-medium text-green-800 mb-1">
                  Name
                </label>
                <input
                  id="name"
                  name="name"
                  type="text"
                  autoComplete="name"
                  required
                  value={form.name}
                  onChange={handleChange}
                  className="w-full px-4 py-2 border rounded-md focus:ring-2 focus:ring-green-400"
                  disabled={status === 'loading'}
                />
              </div>
              <div>
                <label htmlFor="email" className="block font-medium text-green-800 mb-1">
                  Email
                </label>
                <input
                  id="email"
                  name="email"
                  type="email"
                  autoComplete="email"
                  required
                  value={form.email}
                  onChange={handleChange}
                  className="w-full px-4 py-2 border rounded-md focus:ring-2 focus:ring-green-400"
                  disabled={status === 'loading'}
                />
              </div>
              <div>
                <label htmlFor="title" className="block font-medium text-green-800 mb-1">
                  Story Title
                </label>
                <input
                  id="title"
                  name="title"
                  type="text"
                  required
                  value={form.title}
                  onChange={handleChange}
                  className="w-full px-4 py-2 border rounded-md focus:ring-2 focus:ring-green-400"
                  disabled={status === 'loading'}
                />
              </div>
              <div>
                <label htmlFor="content" className="block font-medium text-green-800 mb-1">
                  Story Content
                </label>
                <textarea
                  id="content"
                  name="content"
                  rows={5}
                  required
                  value={form.content}
                  onChange={handleChange}
                  className="w-full px-4 py-2 border rounded-md focus:ring-2 focus:ring-green-400"
                  disabled={status === 'loading'}
                />
              </div>
              <motion.button
                whileHover={{ scale: 1.03 }}
                disabled={status === 'loading'}
                type="submit"
                className="w-full bg-green-700 text-white px-6 py-3 rounded-lg font-semibold hover:bg-green-800 transition"
              >
                {status === 'loading' ? 'Submitting...' : 'Submit Story'}
              </motion.button>
            </form>
            {message && (
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                className={`mt-6 text-center text-lg ${status === 'success' ? 'text-green-700' : 'text-red-600'}`}
              >
                {message}
              </motion.div>
            )}
          </motion.div>
        </motion.div>
      )}
    </AnimatePresence>
  );
}